.footer{
    min-height: 30vh;
    width: 100vw;
    padding-top: 50px;
    .top{
        h1{
            font-size: 5em;
            margin-left: 5%;
            text-align: left;
        }
    }
    .bottom{
        width: 100vw;
        display: flex;
        justify-content: space-between;
        align-items: center;
        p{
            a{
                color: $link-color;
                &:hover{
                    color: #b8b8b8;
                    cursor: pointer;
                }
            }
            font-size: 25px;
        }
        .left{
            margin-left: 5%;
        }
        .right{
            margin-right: 5%;
            display: flex;
            a{
                padding: 10px;
                font-size: 25px;
                text-decoration: none;
                color: $link-color;
                &:hover{
                    color: gray;
                }
            }
        }
    }
    .foot{
        width: 100vw;
        background-color: #222222;
        display: flex;
        justify-content: space-between;
        p{
            margin-right: 50px;
            color: #ffffff;
            font-size: 18px;
            &:nth-child(1){
                margin-left: 50px;
            }
            .bxs-heart{
                color: red;
            }
            a{
             color: $link-color;
                &:hover{
                    filter: hue-rotate(45deg);
                }
            }
        }
    }
}

@media screen and (max-width:800px) {
    .footer{
        .top{
            h1{
                font-size: 3.5em;
            }
        }
        .bottom{
            flex-direction: column;
            
            .right{
                margin-left: 5%;
            }
            
        }
        .foot{
            p{
                font-size: 14px;
            }
        }
    }
}
@media screen and (max-width:800px){
    .footer{
        .foot{
            flex-direction: column;
            justify-content: center;
            align-items: center;
            padding-bottom: 10px;
            p{
                &:nth-child(1){
                    margin: 0;
                    margin-top: 20px;
                    font-size: 18px;
                }
                &:nth-child(2){
                    margin: 0;
                }
            }
        }
    }
}
@media screen and (max-width:400px){
    .footer{
        text-align: left;
        .bottom{
            display: block;
            padding-left: 10px;
            .left{
                a{
                    font-size: 18px;
                    margin-left: -10px;
                }
            }
            .right{
                margin: 0;
                padding-left: 0;
                margin-top: 0px;
                width: 100%;
                padding-bottom: 40px;
            }
        }
        .foot{
            p{
                &:nth-child(1){
                    text-align: center;
                    width: 80%;
                }
            }
        }
    }
    
}